'use client';

import { useState, useEffect } from 'react';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Alert, AlertDescription } from '@/components/ui/alert';
import { mockAnomaService, MockWalletConnection, MockUser } from '@/lib/mockAnomaService';

interface WalletConnectionProps {
  onConnectionChange?: (connected: boolean, user?: MockUser) => void;
}

export default function WalletConnection({ onConnectionChange }: WalletConnectionProps) {
  const [wallet, setWallet] = useState<MockWalletConnection | null>(null);
  const [user, setUser] = useState<MockUser | null>(null);
  const [isConnecting, setIsConnecting] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [mounted, setMounted] = useState(false);

  useEffect(() => {
    setMounted(true);
    // Check if wallet is already connected
    const existingWallet = mockAnomaService.getWalletConnection();
    const existingUser = mockAnomaService.getCurrentUser();
    
    if (existingWallet && existingUser) {
      setWallet(existingWallet);
      setUser(existingUser);
      onConnectionChange?.(true, existingUser);
    }
  }, [onConnectionChange]);

  const handleConnect = async () => {
    setIsConnecting(true);
    setError(null);

    try {
      const connectedWallet = await mockAnomaService.connectWallet();
      const currentUser = mockAnomaService.getCurrentUser();
      
      setWallet(connectedWallet);
      setUser(currentUser);
      onConnectionChange?.(true, currentUser || undefined);
    } catch (err) {
      setError(err instanceof Error ? err.message : 'Failed to connect wallet');
    } finally {
      setIsConnecting(false);
    }
  };

  const handleDisconnect = async () => {
    try {
      await mockAnomaService.disconnectWallet();
      setWallet(null);
      setUser(null);
      onConnectionChange?.(false);
    } catch (err) {
      setError(err instanceof Error ? err.message : 'Failed to disconnect wallet');
    }
  };

  if (!mounted) {
    return null; // Prevent hydration mismatch
  }

  if (wallet && user) {
    return (
      <Card className="mystical-card">
        <CardHeader>
          <CardTitle className="text-theme-white flex items-center gap-2">
            <div className="w-3 h-3 bg-theme-green rounded-full animate-pulse"></div>
            Wallet Connected
          </CardTitle>
          <CardDescription className="text-gray-300">
            Successfully connected to Anoma network
          </CardDescription>
        </CardHeader>
        <CardContent className="space-y-4">
          <div className="space-y-2">
            <div className="text-sm text-gray-400">Wallet Address</div>
            <div className="font-mono text-theme-white bg-theme-black/50 p-2 rounded border border-theme-white/20">
              {wallet.address.slice(0, 6)}...{wallet.address.slice(-4)}
            </div>
          </div>
          
          <div className="space-y-2">
            <div className="text-sm text-gray-400">Network</div>
            <div className="text-theme-green font-medium">{wallet.network}</div>
          </div>

          {user.username && (
            <div className="space-y-2">
              <div className="text-sm text-gray-400">Username</div>
              <div className="text-theme-white font-medium">{user.username}</div>
            </div>
          )}

          <div className="flex gap-2 pt-4">
            <Button 
              onClick={handleDisconnect}
              className="btn-outline flex-1"
            >
              Disconnect
            </Button>
          </div>
        </CardContent>
      </Card>
    );
  }

  return (
    <Card className="mystical-card">
      <CardHeader>
        <CardTitle className="text-theme-white">Connect Your Anoma Wallet</CardTitle>
        <CardDescription className="text-gray-300">
          Connect your wallet to access the decentralized skill exchange network
        </CardDescription>
      </CardHeader>
      <CardContent className="space-y-4">
        {error && (
          <Alert className="border-theme-red bg-theme-red/10">
            <AlertDescription className="text-theme-red">
              {error}
            </AlertDescription>
          </Alert>
        )}

        <div className="space-y-4">
          <div className="text-sm text-gray-300">
            By connecting your wallet, you'll be able to:
          </div>
          <ul className="space-y-2 text-sm text-gray-300">
            <li className="flex items-center gap-2">
              <div className="w-2 h-2 bg-theme-green rounded-full"></div>
              Post teaching and learning intents
            </li>
            <li className="flex items-center gap-2">
              <div className="w-2 h-2 bg-theme-green rounded-full"></div>
              Get matched with compatible peers
            </li>
            <li className="flex items-center gap-2">
              <div className="w-2 h-2 bg-theme-green rounded-full"></div>
              Send encrypted messages
            </li>
            <li className="flex items-center gap-2">
              <div className="w-2 h-2 bg-theme-green rounded-full"></div>
              Control your privacy settings
            </li>
          </ul>
        </div>

        <Button 
          onClick={handleConnect}
          disabled={isConnecting}
          className="btn-primary w-full"
        >
          {isConnecting ? (
            <div className="flex items-center gap-2">
              <div className="w-4 h-4 border-2 border-theme-white border-t-transparent rounded-full animate-spin"></div>
              Connecting...
            </div>
          ) : (
            'Connect Anoma Wallet'
          )}
        </Button>

        <div className="text-xs text-gray-400 text-center">
          This is a demo using mock wallet functionality. 
          In production, this would connect to your actual Anoma wallet.
        </div>
      </CardContent>
    </Card>
  );
}
